<template>
  <!-- <div v-if="Show" class="black-bg">
    <div class="white-bg">
      <img :src="oneRooms[clickModal].image" :alt="oneRooms[clickModal].title">
      <h4>{{oneRooms[clickModal].title}}</h4>
      <p>{{oneRooms[clickModal].content}}</p>
      <p>{{oneRooms[clickModal].price}}원</p>
      <button @click="openModal">닫기</button>
    </div>
  </div> -->

  <!-- emit사용 -->
  <div v-if="Show" class="black-bg">
    <div class="white-bg">
      <img :src="oneRooms[clickModal].image" :alt="oneRooms[clickModal].title">
      <h4>{{oneRooms[clickModal].title}}</h4>
      <p>{{oneRooms[clickModal].content}}</p>
      <input type="text" placeholder="개월수" v-model.number="month"> <!-- @input="month = $event.target.value" 축약형 = v-model -->

      <!-- <input type="number" @input="month = $event.target.value"> -->
      <!-- event.target.value = 사용자가 입력한값 -->
      <!-- @input에 입력할때마다 안에있는 스크립트 실행, @change 입력하고 커서를 다른데 찍었을때 실행-->
      <!-- function(e) { e } 파라미터랑 같은 의미의 $event.target.value -->
      <!--input은 문자자료형이기 때문에 +를 할 경우 문자열 + 문자열을 합쳐줌, 그렇기 때문에 .number와 같은 장식자를 달아줘야됨 -->

      <p>{{ month }}개월 선택함 :{{oneRooms[clickModal].price * month}}원</p>
      <button @click="send()">닫기</button>
    </div>
  </div>

</template>

<script>
export default {
  name: 'CompoModal',
  data(){
    return{
      month:1,
    }
  },
  props:{
    oneRooms : Object,
    Show : Boolean,
    clickModal : Number,
    // openModal: Boolean,
  },
  components : {

  },
  methods : {
    send(){  //함수로 만들어서 전달
      this.$emit('closeModalName');
    }
  },
  watch: {
    // 1방법
    // 감시하고 싶은 data와 같은 이름으로 함수를 만든다.
    month(value){  // 파라미터는 작명하면됨, 2개까지 쓸수있음 왼쪽이 변경될 데이터, 오른쪽은 변경전 데이터
      if(isNaN(value) == true ){  //isNaN() 안에 숫자를 입력하면 false, 글자를 입력하면 true
        alert('숫자만 작성하세요');
        this.month = 1;
      }else if(value >= 13){
        alert('13이상은 금지');
        this.month = 1;
      }
    }
    /* 2방법
    month(value, beforValue){
      if(typeof(value) == 'string'){
        alert('숫자만 작성하세요');
        this.month = 1;
      }else if(value >= 13){
        alert('13이상은 금지');
        this.month = beforValue;
      }
    } */
  },
  // input에 3이하를 썼을때 알림창 띄우기
  beforeUpdate(){
    if(this.month == 2){
      alert('3개월 이상부터 가능');
      this.month = 3;
    }
  },
}
</script>

<style>
.black-bg{
  width:100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed;
  padding:20px;
}
.white-bg{
  width:100%;
  background: #fff;
  border-radius: 8px;
  padding:20px;
}
</style>