<template>
  <div>
  <img alt="Vue logo" src="./assets/logo.png">

  <div class="menu">
    <a href="#" v-for="item in menu" :key="item">{{ item }}</a>
  </div>
  <div class="btn-list">
    <button @click="priceSort">가격순정렬</button>
    <button @click="priceSortRevert">역순정렬</button>
    <button @click="nameSort">가나다순정렬</button>
    <button @click="sortBack">되돌리기</button>
    <button @click="fliter50">50만원이하상품</button>
  </div>

  <!-- component 만들기 -->
  <Discount v-if="showDiscount" :percent="percent"></Discount>

  <!-- 1번숙제 -->
