  </div> -->
  <!-- <Modal :oneRooms="oneRooms" :clickModal="clickModal" :openModal="openModal" :Show="Show"></Modal> -->

  <!-- <div class="start" :class="{end : Show}"> -->
  <!-- css로 transition줘서 사용 true면 end라는 클래스가 들어감 -->
  <transition name="fade">
    <Modal :oneRooms="oneRooms" :clickModal="clickModal" :Show="Show" @closeModalName="openModal(false)"></Modal>
  </transition>


  <!-- 2번숙제 v-for문사용 -->
  <!-- for문을 사용하면서 클릭한 버튼의 내용만 바꾸려면 idx를 사용 -->
  <!--
