  <!-- 컴포넌트화 -->
  <!-- <Card :oneRooms="oneRooms" :openModal="openModal" :click="click"></Card> -->
  <!-- 컴포넌트에 반복문 사용 -->
  <!-- <Card v-for="(oneRoom, idx) in oneRooms" :key="idx" :oneRoom="oneRoom" :openModal="openModal" @click="click(idx)"></Card> -->

  <Card v-for="(oneRooms, idx) in oneRooms" :key="idx" :oneRooms="oneRooms" @openModalName="openModal(true), click($event)"></Card>

  </div> <!-- // end -->
</template>

<script>
import oneRoomData from './assets/oneroom.js';
import Discount from './components/CompoDiscount.vue';
import Modal from './components/CompoModal.vue';
import Card from './components/CompoCard.vue';

export default {
  name: 'App',
  data(){
    return{
      //products : [ '역삼동원룸', '천호동원룸', '마포구원룸' ],
      menu:['Home', 'Shop', 'About'],
      products:[
        {id:1, name: '역삼동원룸', price: 70, num:0, img: require('./assets/room0.jpg')},
        {id:2, name: '천호동원룸', price: 80, num:0, img: require('./assets/room1.jpg')},
        {id:3, name: '마포구원룸', price: 50, num:0, img: require('./assets/room2.jpg')},
      ],
      Show: false,
      oneRooms : oneRoomData,
      //보존되는 원본데이터를 만들면  [...array자료]
      // 그냥 oneRoomsBackup : oneRoomData, 이렇게 썼으면 같은 값을 공유하는 array데이터임
      oneRoomsBackup : [...oneRoomData],

      clickModal:0,
      showDiscount:true,
      percent: 30,
    }
  },
  methods:{
    increase(idx){
      this.products[idx].num++;
    },
    openModal(value){
      // this.Show = !this.Show;
      this.Show = value;
    },
    click(idx){ //모달창 클릭시 클릭한내용의 인덱스를 팝업에 넣기
      this.clickModal = idx
    },
    priceSort(){
      // sort 내부에 function(a, b) { return a - b } 공식-외워 (음수면 a를 왼쪽으로보냄)
      this.oneRooms.sort(function(a,b){
        return a.price - b.price  // 정렬중에서도 가격을 기준으로 정렬한다는뜻
      });
    },
    priceSortRevert(){
      this.oneRooms.sort(function(a,b){
        return b.price - a.price
      });
    },
    nameSort(){
      //방법 1
      //localeCompare() 메서드는 참조 문자열이 정렬 순으로 지정된 문자열 앞 혹은 뒤에 오는지 또는 동일한 문자열인지 나타내는 수치를 반환
      //   this.oneRooms.sort(function(a,b){
      //     return a.title.localeCompare(b.title)
      // });

      // 방법 2
      this.oneRooms.sort(function(a, b) {
          if (a.title < b.title) return -1;
          else if (a.title == b.title ) return 0;
          else return 1;
        }
      );
    },
    sortBack(){
      //sort 원본을 변형시킨다/ map,fliter등은 원본 보존
      // 등호로 array를 집어넣으면 왼쪽 오른쪽 값공유 라는 뜻
      //그래서 ... 이렇게 개별 복사본을 만들어서 넣어줘야함
      this.oneRooms = [...this.oneRoomsBackup];
    },
    fliter50(){
      this.oneRooms = this.oneRooms.filter(function(a){
        return a.price < '500000';
      })
    }
  },

  //서버에서 데이터 요청할때 2곳중 한곳에서 코드짬 -> created , mounted
  //html생성전, 데이터만 존재할때
  created(){
    //서버에서 데이터 가져오는 코드 작성
  },
  //html이 생성
  mounted(){
    //mount(페이지 로드) 후에 뭔가 실행하고 싶으면 여기에 작성
    //되기전에 실행 하고싶으면 beforeMount(){}

     // 여기서 this.를 쓰려면 화살표 함수로 써야 this가 작동됨
    /* setTimeout(() => {
      this.showDiscount = false;
    }, 2000); */

    //1초마다 1%씩 감소 & 0이 되면 멈춤
    let timer = setInterval(()=>{
      this.percent --;
      if(this.percent == 0){
        clearInterval(timer);
      }
    }, 1000);
  },
  components: {
    Discount,
    Modal,
    Card,
  }
}
</script>

